body {
  background-color: firebrick;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: auto;
  width: 1164px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  display: flex;
  height: 100px;
  justify-content: space-around;
}

.description {
  padding: 10px;
}

.description > h1 {
  text-align: center;
}

.items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 10px;
}

.item {
  background-color: white;
  border: 5px solid;
  margin: 10px 20px;
  padding: 5px;
}

.itemTitle {
  text-align: center;
}

.mint {
  text-align: center;
}

.mint > button {
  background-color: white;
  border: 5px solid;
  border-radius: 25px;
  height: 64px;
  margin: 25px;
  width: 250px;
}

.thumbnail {
  height: 150px;
  width: 150px;
}

.thumbnail > img {
  height: inherit;
  object-fit: contain;
  width: inherit;
}

.wallet {
  text-align: center;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.quantity {
  display: flex;
  justify-content: center;
  margin: 10px 0;
}

.quantity-label {
  margin: 0 10px;
}

.quantity-input {
  margin: 0 5px;
}

.quantity-label > span {
  float: right;
}

.quantity-nav {
  background-color: white;
}

.secondaryMessage {
  text-align: center;
}
